import React, { FC } from 'react';
import { Modal as ModalAntd, Typography } from "antd";

export const ModalSubmit: FC<{
  isVisible: boolean,
  onSubmit: ((e: React.MouseEvent<HTMLElement, MouseEvent>) => void) | undefined,
  onCancel: () => void,
  standId?: string,
  title?: string,
  text?: string,
}> = ({ isVisible , onSubmit, onCancel, standId, title, text}) => {

  return (
    <ModalAntd
      title={title}
      visible={isVisible}
      onOk={onSubmit}
      onCancel={onCancel}
      okText=''
      cancelText=''
      centered
    >
      <Typography>
        {standId && ` ,     ${standId}?`}
        {text}
      </Typography>
    </ModalAntd>
  );
};
